
'use client';

import { useState, useEffect, useMemo } from 'react';
import type { User, Building, Competition } from '@/types';
import { getRawBuildings, getUserSession } from '@/app/actions';
import WorksFilterControls from '@/components/filters/WorksFilterControls';
import BuildingCard from '@/components/minecraft/BuildingCard';
import { Skeleton } from '@/components/ui/skeleton';
import { Card, CardContent, CardHeader, CardFooter } from '@/components/ui/card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { GalleryVerticalEnd } from 'lucide-react';


export default function HomePage() {
  const [allWorks, setAllWorks] = useState<Building[]>([]);
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchData() {
      setIsLoading(true);
      setError(null);
      try {
        // Pass undefined to getRawBuildings so it fetches session if needed internally
        const worksData = await getRawBuildings(undefined); 
        const sessionUser = await getUserSession(); // Still needed for currentUser state here
        
        setAllWorks(worksData);
        setCurrentUser(sessionUser);
      } catch (err) {
        console.error("Error fetching homepage data:", err);
        setError("无法加载作品数据，请稍后再试。");
      } finally {
        setIsLoading(false);
      }
    }
    fetchData();
  }, []);

  const availableYears = useMemo(() => {
    if (!allWorks || allWorks.length === 0) return [];
    const years = new Set(allWorks.map(work => work.eventYear).filter(year => year != null));
    return Array.from(years).sort((a, b) => b - a);
  }, [allWorks]);

  if (error) {
    return (
      <Alert variant="destructive" className="my-6">
        <AlertTitle>数据加载错误</AlertTitle>
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    );
  }
  
  return (
    <div className="space-y-6">
      <h2 className="text-2xl font-semibold text-foreground tracking-tight flex items-center mb-4">
        <GalleryVerticalEnd className="mr-3 h-7 w-7 text-primary" />
        作品画廊
      </h2>

      {isLoading ? (
        <>
          <Card className="mb-6 shadow-sm border-border/30 bg-card/50">
            <CardContent className="p-3 sm:p-4 space-y-3 md:space-y-0 md:flex md:flex-wrap md:items-end md:gap-3">
              <div className="flex-grow min-w-[180px] md:max-w-xs lg:max-w-sm">
                <Skeleton className="h-4 w-20 mb-1 bg-muted-foreground/20" />
                <Skeleton className="h-9 w-full bg-muted-foreground/20" />
              </div>
              <div className="min-w-[120px] sm:min-w-[140px] md:flex-shrink-0">
                <Skeleton className="h-4 w-16 mb-1 bg-muted-foreground/20" />
                <Skeleton className="h-9 w-full bg-muted-foreground/20" />
              </div>
              <div className="min-w-[120px] sm:min-w-[140px] md:flex-shrink-0">
                <Skeleton className="h-4 w-16 mb-1 bg-muted-foreground/20" />
                <Skeleton className="h-9 w-full bg-muted-foreground/20" />
              </div>
            </CardContent>
          </Card>
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
            {[...Array(4)].map((_, i) => (
              <Card key={i} className="bg-card border-border overflow-hidden shadow-lg flex flex-col h-full">
                <Skeleton className="aspect-[16/9] w-full bg-muted-foreground/20" />
                <CardHeader className="pt-4 pb-2">
                  <Skeleton className="h-6 w-3/4 mb-2 bg-muted-foreground/20" />
                  <Skeleton className="h-4 w-1/2 bg-muted-foreground/20" />
                </CardHeader>
                <CardContent className="flex-grow pt-1 pb-3 space-y-2">
                  <Skeleton className="h-4 w-full bg-muted-foreground/20" />
                  <Skeleton className="h-4 w-5/6 bg-muted-foreground/20" />
                </CardContent>
                <CardFooter className="mt-auto pt-3 pb-4 px-4 border-t border-border/50">
                  <Skeleton className="h-8 w-full bg-muted-foreground/20" />
                </CardFooter>
              </Card>
            ))}
          </div>
        </>
      ) : (
        <WorksFilterControls 
          initialWorks={allWorks} 
          currentUser={currentUser} 
          availableYears={availableYears} 
          showYearFilter={true}
          showSizeFilter={true}
        />
      )}
    </div>
  );
}

